<template>
  <el-menu class="el-menu-demo" mode="horizontal"  style="border-bottom: 0;"
           @select="handleSelect"
  >
    <el-submenu index="3" :disabled="powerMenu">
      <template slot="title"><span>学期切换</span></template>
      <el-submenu v-bind:index="'3-'+(1+i)" v-for="(singleTerm,i) in termInfo.termList" :key="i" >
        <template slot="title"><i :class="singleTerm.termKey===termInfo.clickTerm? 'el-icon-success':''"></i>{{singleTerm.termValue}}
        </template>
        <el-menu-item v-bind:index="'3-'+(i+1)+'-'+(j+1)" v-for="(singleTermChildren,j) in singleTerm.termChildren" :key="j" @click="setCurrentTerm(singleTerm,singleTermChildren)"><i :class="singleTermChildren.termChildrenKey===termInfo.clickChildrenTerm? 'el-icon-success':''"></i>
          {{singleTermChildren.value}}
        </el-menu-item>
      </el-submenu>
    </el-submenu>
    <el-submenu index="4" style="float: right;margin-right: 5px;">
      <template slot="title"><span>姓名: {{user.user_name}}</span></template>
      <el-menu-item index="4-1" @click="dialogVisible = true"><i class="el-icon-edit"></i>修改密码</el-menu-item>

      <el-menu-item index="4-2" @click="exitAccount"><i class="el-icon-switch-button"></i>退出登录</el-menu-item>
    </el-submenu>

    <el-dialog
      title="密码修改"
      :visible.sync="dialogVisible"
      width="30%"
      >
      <div>
        <el-input placeholder="请输入新密码:" style="margin-bottom: 15px;width: 270px;" show-password></el-input>
        <el-input placeholder="请再输入一次新密码:" style="width: 270px;"></el-input>
      </div>

      <span slot="footer" class="dialog-footer">
    <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
    <el-button @click="dialogVisible = false">取 消</el-button>
  </span>
    </el-dialog>

  </el-menu>

</template>

<script>
export default {
  name: 'head-menu-part-two',
  created () {
    // console.log(this.$store.state.termInfo)
  },
  props: { // 传值teacher对象信息
    user: {
      type: Object,
      default: () => {
        return {user_name: '服务器错误'}
      }
    },
    powerMenu: {
      type: Boolean,
      default: () => {
        return false
      }
    }
  },
  data () {
    return {
      dialogVisible: false,
      termInfo: this.$store.getters.getTermInfo
    }
  },
  methods: {
    handleSelect (key, keyPath) {
      // console.log(key, keyPath)
    },
    setCurrentTerm (term, termChild) {
      // console.log(term.termKey, termChild.termChildrenKey)
      this.$store.commit('setClickChildrenTerm', {clickTerm: term.termKey, clickChildrenTerm: termChild.termChildrenKey})
      // this.termInfo = this.$store.getters.getTermInfo
      // console.log(this.$store.state.termInfo)
      this.$router.go(0)
    },
    exitAccount () {
      if (this.$cookies.isKey('user_info')) {
        this.$cookies.remove('user_info')
        this.$router.push({path: '/login'})
      } else {
        this.$router.push({path: '/login'})
      }
    }
  }}
</script>

<style scoped>
</style>



